<template>
  <div>
    <NavTemplent title="报告页面"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <el-form-item label="购买方式">
            <el-select v-model="form.buyType" placeholder="请输入姓名">
              <el-option label="Hour" :value="0"></el-option>
              <el-option label="None" :value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="text-align: right">
            <el-button type="primary">
              <i class="el-icon-search"></i>
              筛选</el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-row>
        <el-col :span="24">
          <div id="myChart" style="width: 100%;height: 400px"></div>
        </el-col>
      </el-row>
      <div class="tableTitle">
        <span style="width: 100%;float: left">
          <div>报告列表</div>
        </span>
      </div>
      <el-table
        :data="logisticsList"
        border
        style="width: 100%">
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          label="日志">
          <template slot-scope="scope">
            <span>{{scope.row.warehouseOrderCode || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="订单">
          <template slot-scope="scope">
            <span>{{scope.row.shippingMethodNo || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="销售金额">
          <template slot-scope="scope">
            <span>{{scope.row.orderWeight || '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="打折">
          <template slot-scope="scope">
            <span>{{scope.row.shippingMethod}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="退货">
          <template slot-scope="scope">
            <span>{{ scope.row.shipFee}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="净销售额">
          <template slot-scope="scope">
            <span>{{ scope.row.dateWarehouseShipping | dateFormat('YYYY/MM/DD')}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="运输">
          <template slot-scope="scope">
            <span>{{ scope.row.platformFeeTotal}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="税额">
          <template slot-scope="scope">
            <span>{{ scope.row.platformFeeTotal}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="总销售额">
          <template slot-scope="scope">
            <span>{{ scope.row.platformFeeTotal}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    export default {
        components: {
            NavTemplent
        },
        name: "index",
        data() {
            return {
                logisticsList: [],
                form: {
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                    buyType: 0
                }
            }
        },
        methods: {
            drawLine() {
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                // 绘制图表
                myChart.setOption({
                    title: {text: '图标展示'},
                    color: ['#3398DB'],
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis : [
                        {
                            type : 'category',
                            data : ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00', '24:00'],
                            axisTick: {
                                alignWithLabel: true
                            }
                        }
                    ],
                    yAxis : [
                        {
                            type : 'value'
                        }
                    ],
                    series : [
                        {
                            name:'直接访问',
                            type:'bar',
                            barWidth: '60%',
                            data:[10, 52, 20, 33, 30, 33, 22, 43, 23, 65, 34, 76, 87, 65, 43, 32, 43, 21, 54, 76, 54, 46, 54, 97, 90]
                        }
                    ]
                });
            },
        },
        mounted() {
            this.drawLine();
        }
    }
</script>

<style scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
  div{
    font-weight: bolder;
  }
  }
  .el-select{
    width: 100%;
  }
</style>



